<template>
  <div class="ui container centered very padded">
  <pulse-loader class="ui container very padded" :loading="loading" :color="loaderColor" :size="loaderSize"></pulse-loader>
  <div class="text container center" v-if="!loading">
    <h1 class="ui header very padded">Spaces List</h1>
    <div class="ui segment very padded">
    <div class="ui big middle aligned list divided" v-for="space in userSpaces">
      <div class="ui item">
          <div class="heading">
            <router-link v-bind:to="'/space/' + space._id" class="header">{{space.displayName}}</router-link>
          </div>
      </div>
    </div>
  </div>
  </div>
  </div>
</template>

<script>
import {mapState, mapActions} from 'vuex'

export default {
  data () {
    return {
      loading: true
    }
  },
  computed: mapState(['isAuthenticated','userSpaces','loaderColor','loaderSize']),
  methods: mapActions(['loadSpaces']),
  mounted: function () {
      this.loadSpaces()
    },
  watch: {
    userSpaces: function( array ) {
      if(array.length >0)
        this.loading = false
    }
  }
}
</script>

<style>

</style>



