<template>
  <div class="ui container">
    <pulse-loader class="text container center" :loading="loading" :color="loaderColor" :size="loaderSize"></pulse-loader>
    <div v-show="!loading" class="ui segment raised very padded">
      <h1 class="ui icon header">
        <i class="privacy icon"></i>
        <div class="content">
          You have successfully logged out.
        </div>
      </h1>
    </div>
  </div>
</template>

<script>
import {mapState, mapActions} from 'vuex'

export default {
  data () {
    return {
      loading: true
    }
  },
  computed: mapState(['isAuthenticated','loaderSize','loaderColor']),
  methods: mapActions(['logOut']),
  mounted: function () {
    this.logOut()
  },
  watch: {
    isAuthenticated: function(auth) {
      if(!auth)
        this.loading = false
    }
  }
}
</script>

<style>

</style>



